.detailExam {
  background: #f2f2f2;
  position: relative;
  padding: 40px 40px;
  border-top-right-radius: 15px;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;

  top: 2px;
  // float: left;
  text-align: left;
  margin: 80px auto 0 auto;
  width: 65%;
  min-height: 400px;
  display: flex;
  height: 500px;

  .iconVideoo {
    position: absolute;
    top: -40px;
    left: 0;
    background: #f2f2f2;
    display: flex;
    height: 40px;
    padding: 0 20px;
    align-items: center;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
    p {
      margin: 0px 0 0 10px;
    }
  }

  .detail {
    padding: 5px;
    display: flex;
    width: 100%;
    .detailInfo {
      width: 300px;
      .TypeP {
        margin-bottom: 42px;
      }
      p {
        text-align: left;
        // text-indent: 10px;
        color: #666666;
        span {
          font-weight: 600;
        }
      }
      div {
        .btn {
          margin: 0;
          background: #00aff0;
          outline: 0;
          &:hover {
            color: white;
          }
        }
      }
    }
    .mainMessageDiv {
      width: 70%;
      .subjectDiv {
        display: flex;
        justify-content: space-between;
        align-items: center;
        background: #fa9551;
        color: white;
        border-top-left-radius: 15px;
        border-top-right-radius: 15px;
        height: 40px;
        p {
          margin: 5px 15px 0 15px;
          span {
            font-weight: 700;
          }
        }
      }
      .messageDiv {
        height: 75%;
        width: 100%;
        background: white;
        border-bottom-left-radius: 15px;
        border-bottom-right-radius: 15px;
        padding: 10px 13px 0 13px;
        textarea {
          width: 100%;
          height: 100%;
          // padding: 10px;
          background: white;
          // text-indent: 10px;
          border: none;
          resize: none;
          border-bottom-left-radius: 15px;
          border-bottom-right-radius: 15px;
          &::-webkit-scrollbar {
            width: 5px;
          }

          &::-webkit-scrollbar-button:single-button {
            // background-color: #bbbbbb;
            display: block;

            height: 13px;
            width: 16px;
          }

          &::-webkit-scrollbar-track {
            background: #f1f1f1;
            border-radius: 5px;
          }

          &::-webkit-scrollbar-thumb {
            background: #fa9551;
            border-radius: 5px;
          }

          &::-webkit-scrollbar-thumb:hover {
            background: #db8246;
          }
        }
      }
    }
  }

  .detail2 {
    color: #666666;
    display: flex;
    .detailInfo2 {
      width: 35%;
      padding: 20px 10px 10px 10px;
      overflow: hidden;
      .ClientP {
        text-indent: 10px;
        span {
          font-weight: 700;
        }
      }
      .MainIconsDiv {
        display: flex;
        justify-content: space-around;
        padding: 10px 0;
        img {
          &:hover {
            cursor: pointer;
          }
        }
      }
      .MainDivForChat {
        p {
          background: #fa9551;
          color: white;
          height: 30px;
          text-indent: 10px;
          font-weight: 700;
          border-top-left-radius: 10px;
          border-top-right-radius: 10px;
          display: flex;
          align-items: center;
          margin: 0;
        }
        #messages {
          height: 150px;
          margin: 0;
          border: 0;
          padding: 20px !important;
          p {
            position: relative;
            margin-top: 25px !important;
            margin-bottom: 20px !important;
            span {
              display: flex;
              position: absolute;
              top: -18px;
              left: -2px;
              width: 48px;
              text-indent: 0;
            }
          }

          &::-webkit-scrollbar {
            width: 5px;
          }

          /* Track */
          &::-webkit-scrollbar-track {
            background: #f1f1f1;
            border-radius: 5px;
          }

          /* Handle */
          &::-webkit-scrollbar-thumb {
            background: #e6e6e6;
            border: 1px solid white;
            border-radius: 5px;
          }

          /* Handle on hover */
          &::-webkit-scrollbar-thumb:hover {
            background: #1184c7;
          }
          p {
            font-weight: 400 !important;
            color: #666666;
          }
        }
        .inputMessage {
          #send {
            visibility: hidden;
            height: 10px;
          }
          #yourMessage {
            border: 0;
            width: 100%;
            height: 30px;
            text-indent: 10px;
          }
        }
      }
      #myVid {
        object-fit: cover;
        width: 31.5% !important;
        border-radius: 15px;

        video {
          // height: 100px !important;
          width: 400px !important;
          height: 170px !important;
          background: #e6e6e6;
        }
      }
    }
    #videoChat {
      display: flex;
      justify-content: center;
      height: 500px;
      width: 70%;
      padding: 10px 10px 10px 0;
      // position: relative;
      // background: green;
      .vid {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 15px;
        // position: absolute;
        // // right: -20px
        // top: 7px;
      }

      .extendScreen {
        position: absolute;
        z-index: 5;
        top: 450px;
        right: 20px;
        &:hover {
          cursor: pointer;
        }
      }
    }
  }

  .record-box {
    padding: 5px;
    h4 {
      margin: 0 auto 12px auto;
      color: #4092c2;
    }
    p {
      text-align: left;
      text-indent: 10px;
      color: #4092c2;
      span {
        font-weight: bold;
      }
    }
  }
  .message-btn {
    position: absolute;
    bottom: 20px;
    left: 35px;
    h3 {
      padding: 0 0 0 10px;
    }
    .btn {
      padding: 12px;
      border-radius: 8px;
      background-color: #00aff0;
      color: white;
      margin: 10px;
      font-size: 18px;
      transition: all 0.5s ease-in-out;
      outline: 0;

      &:disabled,
      button[disabled] {
        background: lightgray;
        color: white;
        cursor: not-allowed;
        box-shadow: none;
        pointer-events: none;
        border: none;
      }
    }
  }
}

.recordsDetail {
  h4 {
    margin-top: 30px;
  }
  width: 60%;
  color: #4092c2;
  margin: 0 auto 167px auto;
  .report {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));

    width: 100%;
    .report-details {
      border: 1px solid #4092c2;
      border-radius: 5px;
      padding: 10px;
      margin: 20px 5px 20px 5px;
      p {
        border: none;
        span {
          font-weight: bold;
        }
      }
    }
  }
}

.report {
  width: 50%;
  padding: 5px;

  .report-details {
    h4 {
      color: #4092c2;
    }
    p {
      text-align: left;
      border-bottom: 1px solid #4092c2;
      text-indent: 10px;
      color: #4092c2;
    }
  }
}

